.btn:disabled {
  cursor: not-allowed;
}

.btn-play:disabled {
  background-color: #0f1842;
}

.btn-play:disabled svg {
  opacity: 0.25;
}

.btn:active {
  opacity: 0.5;
}

.toaster-container {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 45vh;
  width: calc(100vw - 10px);
  padding: 17px;
  padding-right: 0;
  margin: 0 5px;
  background-color: var(--primaryFg);
  color: var(--primaryBg);
  height: 50px;
  align-items: center;
}

.toaster-btns {
  display: flex;
  height: 100%;
  align-items: center;
}

.toaster-btns button {
  padding: 16px;
  color: var(--primaryBg);
  text-transform: uppercase;
}

.toaster-btns button:first-child {
  margin-right: 10px;
}

.settings-bar {
  display: flex;
  padding: 3px;
  margin: auto;
  border-radius: 5px;
  width: calc(100vw - 5px);
  background-color: darkgray;
}

.repeat-disabled {
  opacity: 0.3;
}

@media only screen and (orientation: landscape) {
  .btn-play span {
    font-size: 2em;
  }

  .time-display {
    position: relative;
    margin-top: 10px;
    font-size: 15rem;
    line-height: 12rem;
  }

  .btn-stop {
    top: 50vh;
  }

  .input-audio + label {
    left: 0;
    bottom: calc(15vh + 10px);
  }
}
